"use client"

import { useState } from "react"
import { motion } from "framer-motion"

export default function Navbar() {
  const [open, setOpen] = useState(false)

  const scrollToSection = (sectionId) => {
    const element = document.getElementById(sectionId)
    if (element) {
      element.scrollIntoView({ behavior: 'smooth', block: 'start' })
    }
    setOpen(false) // Close mobile menu after clicking
  }

  return (
    <motion.nav
      initial={{ y: -20, opacity: 0 }}
      animate={{ y: 0, opacity: 1 }}
      transition={{ duration: 0.5 }}
      className="
        fixed top-0 left-0 w-full z-[999]
        bg-white/95 backdrop-blur-md
        border-b border-slate-200
        shadow-sm
      "
    >
      <div className="w-full flex justify-between items-center py-4 px-8 md:px-12 lg:px-16">
        
        {/* Logo - More to the left */}
        <button 
          onClick={() => scrollToSection('hero')}
          className="text-2xl font-bold text-green-600 hover:text-green-700 transition-colors cursor-pointer"
        >
          Venturemond
        </button>

        {/* Desktop Navigation - More to the right */}
        <div className="hidden md:flex items-center space-x-8 ml-auto">
          <button 
            onClick={() => scrollToSection('hero')}
            className="text-slate-700 hover:text-green-600 font-medium transition-colors cursor-pointer"
          >
            Home
          </button>
          <button 
            onClick={() => scrollToSection('services')}
            className="text-slate-700 hover:text-green-600 font-medium transition-colors cursor-pointer"
          >
            Services
          </button>
          <button 
            onClick={() => scrollToSection('projects')}
            className="text-slate-700 hover:text-green-600 font-medium transition-colors cursor-pointer"
          >
            Projects
          </button>
          <button 
            onClick={() => scrollToSection('about')}
            className="text-slate-700 hover:text-green-600 font-medium transition-colors cursor-pointer"
          >
            About
          </button>
          <button 
            onClick={() => scrollToSection('contact')}
            className="
              px-6 py-2.5 
              bg-green-600 text-white 
              font-semibold rounded-lg
              hover:bg-green-700 
              transition-all duration-300
              shadow-sm hover:shadow-md
              cursor-pointer
            "
          >
            Contact
          </button>
        </div>

        {/* Mobile Menu Button */}
        <button
          onClick={() => setOpen(!open)}
          className="md:hidden text-slate-700 hover:text-green-600 transition-colors"
          aria-label="Toggle menu"
        >
          <svg 
            className="w-6 h-6" 
            fill="none" 
            stroke="currentColor" 
            viewBox="0 0 24 24"
          >
            {open ? (
              <path 
                strokeLinecap="round" 
                strokeLinejoin="round" 
                strokeWidth={2} 
                d="M6 18L18 6M6 6l12 12" 
              />
            ) : (
              <path 
                strokeLinecap="round" 
                strokeLinejoin="round" 
                strokeWidth={2} 
                d="M4 6h16M4 12h16M4 18h16" 
              />
            )}
          </svg>
        </button>
      </div>

      {/* Mobile Menu */}
      {open && (
        <motion.div
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          exit={{ opacity: 0, y: -20 }}
          className="md:hidden bg-white border-t border-slate-200 shadow-lg"
        >
          <div className="flex flex-col space-y-4 px-6 py-6">
            <button 
              onClick={() => scrollToSection('hero')}
              className="text-slate-700 hover:text-green-600 font-medium transition-colors py-2 text-left"
            >
              Home
            </button>
            <button 
              onClick={() => scrollToSection('services')}
              className="text-slate-700 hover:text-green-600 font-medium transition-colors py-2 text-left"
            >
              Services
            </button>
            <button 
              onClick={() => scrollToSection('projects')}
              className="text-slate-700 hover:text-green-600 font-medium transition-colors py-2 text-left"
            >
              Projects
            </button>
            <button 
              onClick={() => scrollToSection('about')}
              className="text-slate-700 hover:text-green-600 font-medium transition-colors py-2 text-left"
            >
              About
            </button>
            <button 
              onClick={() => scrollToSection('contact')}
              className="
                px-6 py-3 
                bg-green-600 text-white 
                font-semibold rounded-lg
                hover:bg-green-700 
                transition-all duration-300
                text-center
                shadow-sm
              "
            >
              Contact
            </button>
          </div>
        </motion.div>
      )}
    </motion.nav>
  )
}
